@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Oswald', sans-serif;
}
p,
a {
  font-family: 'Open Sans', sans-serif;
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#home {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #183893;
}
.intro {
  margin-bottom: 2rem;
}
#home h2 {
  color: #ff6d65;
  font-size: 70px;
  text-align: center;
  line-height: 1.2;
  margin-bottom: 1rem;
}
#home p {
  color: white;
  font-size: 32px;
  text-align: center;
  font-weight: 200;
}
.icons {
  display: flex;
  width: 200px;
  justify-content: center;
  align-items: center;
}
.social-media {
  width: 25px;
  height: 25px;
  margin: 0.5rem;
}
.cls-1 {
  fill: white;
  transition: fill 0.2s ease-in-out;
}
.social-media:hover .cls-1 {
  fill: #ff6d65;
}
@media screen and (max-width: 420px) {
  #home h2 {
    font-size: 50px;
  }
  #home p {
    font-size: 24px;
  }
}

.nav-bar {
  position: absolute;
  width: 100vw;
  height: 50px;
  z-index: 90;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  transition: all 0.3s ease-in-out;
  margin-top: 1rem;
}
.nav-bar.fixed {
  position: fixed;
  background-color: #0b132b;
  margin-top: 0;
}
.nav-bar.fixed li a {
  color: white;
}
.nav-bar.fixed li a .underline {
  background-color: white;
}
.nav-bar li {
  list-style: none;
}
.nav-bar li a {
  margin-right: 6rem;
  display: inline-block;
  text-decoration: none;
  color: white;
  font-family: 'Oswald', sans-serif;
  font-size: 22px;
  font-weight: 600;
  text-transform: lowercase;
  position: relative;
  padding: 0.2em;
  overflow: hidden;
  text-transform: uppercase;
}
.nav-bar li a .underline {
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: white;
  transform: scaleX(0) translateX(-100%);
  transform-origin: left center;
  transition: transform 0.3s ease-in-out;
}
.nav-bar li a:hover .underline {
  transform: scaleX(1) translateX(0%);
}
@media screen and (max-width: 768px) {
  .nav-bar {
    justify-content: space-evenly;
  }
  .nav-bar li a {
    margin-right: 0;
  }
}

.loader-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #183893;
}
.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #ff6d65 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

